body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Lato", "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.layout {
  display: flex;
}

.content {
  flex: 1; /* Take remaining height */
  overflow-y: auto;
}

.navbar {
  width: 170px;
  height: 100vh;
  min-height: 200px;
  overflow: auto;
  background: #264653;
  color: #fff;
  position: -webkit-sticky;
  position: sticky;
  top: 1px;
  font-size: 16px;
}

.navbar-menu {
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.navbar-menu-item {
  padding: 10px 20px;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;

  &:hover {
    background-color: #173542;
  }
}

.navbar-avatar {
  width: 100px;
  height: 100px;
  border-bottom: "5px solid #ddd";
  background-color: #f9f9f9;

  margin: 10px auto;
  background: #4f4f4f;

  img {
    width: 100%;
    height: 100%;
    border-radius: 15px;
    background: #4f4f4f;
  }
}

.navbar-icon {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }
}

.section-title {
  font-family: "Lato", sans-serif;
  font-weight: 200;
  margin-top: 0;
  padding: 30px 40px;
  text-transform: uppercase;
  background-color: #22577a;
  color: #fff;
}

.section-content {
  font-family: "Lato", sans-serif;
  display: flex;
  justify-content: center;
  width: 80%;
  margin-top: 10px;
  margin-bottom: 10px;
  margin: 30px auto;
  background-color: #f6f6f6;
}

.about-us {
  font-family: "Lato", sans-serif;
  padding: 30px 40px;
  p {
    font-size: 1.2em;
    line-height: 1.5em;
  }
  span {
    font-weight: 600;
  }
}

.experience-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.experience-card {
  font-family: "Lato", sans-serif;
  position: relative;
  z-index: 10;
  border: 2px solid #fff;
  border-radius: 10px;
  padding: 20px;
  background-color: #ffff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 800px;
  margin: 20px;
}

.experience-card li {
  font-size: 1em;
  line-height: 1.5em;
}

.experience-card-header {
  font-family: "Lato", sans-serif;
  display: flex;
  justify-content: space-between;
  align-content: center;
}

.experience-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.experience-logo-section {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 20px;
}

.experience-logo-section p {
  font-size: 16px;
  line-height: 24px;
  font-weight: 700;
  font-style: "lato";
}

.role {
  text-align: left;
}

.company-logo {
  font-family: "Lato", sans-serif;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }
}

.project-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  width: 100%;
  max-width: 1200px;
  padding: 40px 0;
}

.card {
  font-family: "Lato", sans-serif;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  text-align: center;
  padding: 10px;
}

.card img {
  width: 100%;
  height: 240px;
}

.card h2 {
  font-size: 1.5em;
  margin: 16px 0;
}

.card p {
  padding: 0 16px 16px;
  font-size: 1em;
  color: #333;
}

.skill-container {
  font-family: "Lato", sans-serif;
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 35px;
}

.skill-grid {
  font-family: "Lato", sans-serif;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  padding: 20px;
  margin: 20px 0;
}

.skill-title {
  margin-bottom: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid #ccc;
}

.skill-title h1 {
  font-size: 2em;
}

.skill-list-grid {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 20px;
}

.skill-card {
  background-color: #fff;
  border-radius: 8px;
  overflow: hidden;
  text-align: center;
  padding: 16px;
}

.skill-card img {
  width: 100%;
  height: 120px;
  max-width: 200px;
  margin: 0 auto;
  display: block;
}

.skill-card p {
  margin-top: 10px;
  font-size: 1em;
  color: #333;
}
.education-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  width: 100%;
  max-width: 1200px;
  padding: 40px 0;
}

.education-card {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  text-align: left;
  padding: 20px;
}

.school-name {
  font-size: 1.5em;
  margin: 16px 0;
  color: #262626;
  text-decoration: none;
}

.contact {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 20px;
}

.contact-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
  margin: 10px 0;
}

.contact-item img {
  width: 25px;
  height: 25px;
}

.contact-item a {
  color: #262626;
  font-size: 1.2em;
  text-decoration: none;
}
